import React from 'react'
import {
  CButton,
  CCard,
  CCardBody,
  CListGroup,
  CCol,
  CListGroupItem,
  CRow 
} from '@coreui/react';
import Preview from './Preview'
import {requestOptions, announcementUrl} from '../Config'

class Announcement extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      announcementList: [],
      previewData:[]
    };
  }
  componentDidMount() {
    fetch(announcementUrl, requestOptions)
    .then((response) => response.json())
    .then((data) => {this.setState({announcementList:data})  })
    .catch(error => console.log("Error detected: " + error)) ;
  }

   handleSort = (data)=>{
    this.setState({previewData: data})
  }
  render() {
    return <CRow>
              <CCol xl="4" sm="4">
                <strong className="text-info">Today</strong>
                <CCard>
                   <CCardBody>
                      <CListGroup>
                            {this.state.announcementList.map((list) => (
                            <CListGroupItem action key={list.id} onClick={()=>this.handleSort(list)}>
                            <h6 className="font-weight-bolder" style={{color:'black'}}>{list.title}
                                  <small className="ml-2 text-muted">5 min {'>'}</small>
                            </h6>
                            <div>  {list.body} </div>
                            </CListGroupItem>
                              ))}
                      </CListGroup>
                  </CCardBody>
                </CCard>
            </CCol>
            <CCol xl="8" sm="8">
             <Preview previewData={this.state.previewData}/>
            </CCol>
    </CRow>
  }
}

export default Announcement
